.hero {
  position: relative;
  background-color: #ffb8ca;
  overflow: hidden;
  &__img{
	  width: 100%;
	  height: auto;
  }

  &__container {
    position: relative;
    z-index: 1;
    margin: 0 auto;
    @media screen and (max-width: 767px) {
      max-width: 320px;
      padding: 0 20px;
    }
    @media screen and (min-width: 768px) and (max-width: 1279px) {
      max-width: 768px;
      padding: 0 34px;
    }
    @media screen and (min-width: 1280px) {
      max-width: 1280px;
      padding: 0 97px 0 46px;
    }
  }

  &__text {
    position: relative;
    z-index: 2;
    @media screen and (max-width: 767px) {
		padding-top: 137px;
		width: 218px;
    }
    @media screen and (min-width: 768px) {
      width: 162px;
      padding-top: 15px;
    }
    @media screen and (min-width: 1280px) {
      width: 277px;
	  padding-top: 84px;
      margin-left: 69px;
    }

    &__header {
      max-width: 218px;
      font-family: Titan One;
      font-style: normal;
      font-weight: normal;
      font-size: 26px;
      line-height: 30px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: #ffffff;
      margin-bottom: 19px;

      @media screen and (min-width: 767px) {
        font-family: Titan One;
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 25px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #ffffff;
      }
      @media screen and (min-width: 1280px) {
        max-width: 262px;
        font-family: Titan One;
        font-style: normal;
        font-weight: normal;
        font-size: 38px;
        line-height: 44px;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        color: #ffffff;
      }

      & > span {
        font-family: Titan One;
        font-style: normal;
        font-weight: normal;
        font-size: 48px;
        line-height: 96.5%;

        text-transform: uppercase;

        color: #d41443;
        @media screen and (min-width: 767px) {
          font-family: Titan One;
          font-style: normal;
          font-weight: normal;
          font-size: 34px;
          line-height: 90.5%;
          /* or 31px */

          letter-spacing: 0.04em;
          text-transform: uppercase;

          color: #d41443;
        }
        @media screen and (min-width: 1280px) {
          font-family: Titan One;
          font-style: normal;
          font-weight: normal;
          font-size: 58px;
          line-height: 90.5%;
          /* or 52px */

          letter-spacing: 0.04em;
          text-transform: uppercase;
        }
      }
    }
  }
  &__nav {
    display: none;
    @media screen and (min-width: 768px) {
      display: none;
    }
    @media screen and (min-width: 1280px) {
      display: inline-block;
    }
  }

  // images position absolute
  &__cirkle {
    position: absolute;
    z-index: -1;
    @media screen and (max-width: 767px) {
      bottom: 75px;
      right: -62px;
      width: 346px;
      height: 344px;
    }
    @media screen and (min-width: 768px) {
      width: 324px;
      height: 322px;
      bottom: 46px;
      right: 137px;
    }
    @media screen and (min-width: 1280px) {
      width: 538px;
      height: 538px;
      bottom: 75px;
      right: 237px;
    }
  }
  &__ice {
    position: absolute;
    bottom: 0;


    @media screen and (max-width: 767px) {
      right: 50px;

      width: 171px;
      height: 410px;
    }
    @media screen and (min-width: 768px) {
      right: 247px;

      width: 143px;
      height: 336px;
    }
    @media screen and (min-width: 1280px) {
      width: 236px;

      height: 557px;
      right: 450px;
    }
  }
  &__milk-text {
    width: 114px;
    position: absolute;
    bottom: 57px;
    left: 158px;
    @media screen and (max-width: 767px) {
      display: none;
    }
    @media screen and (min-width: 1280px) {
      left: 264px;
      bottom: 82px;
    }
    &__text {
      font-family: DM Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 8px;
      line-height: 150%;
      /* or 12px */

      color: #ffffff;
      @media screen and (min-width: 768px) {
        width: 162px;
        padding-top: 15px;
      }
      @media screen and (min-width: 1280px) {
        width: 200px;
        font-family: DM Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 140%;
        /* or 20px */
        margin-bottom: 20px;
        color: #ffffff;
      }
    }
    &__arrow {
      position: absolute;
      bottom: -25px;
      height: 18px;
      width: 18px;
      @media screen and (min-width: 1280px) {
        height: 26px;
        width: 26px;
      }
    }
  }

  &__info {
    position: absolute;

    top: 53px;
    right: 48px;
    @media screen and (min-width: 1280px) {
      top: 90px;
      right: 97px;
    }
    &__item {
      position: relative;
      display: flex;
      flex-direction: column;
      font-family: DM Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 8px;
      line-height: 10px;
      text-align: right;
      color: #ffffff;
      margin-bottom: 13px;
      @media screen and (min-width: 1280px) {
        font-family: DM Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        text-align: right;

        color: #ffffff;
      }
      & > span {
        font-family: Titan One;
        font-style: normal;
        font-weight: normal;
        font-size: 28px;
        line-height: 32px;
        /* identical to box height */

        text-align: right;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        color: #d41443;
        @media screen and (min-width: 1280px) {
          font-family: Titan One;
          font-style: normal;
          font-weight: normal;
          font-size: 46px;
          line-height: 53px;
          /* identical to box height */

          text-align: right;
          letter-spacing: 0.04em;
          text-transform: uppercase;

          color: #d41443;
        }
      }
    }
    &__icon {
      position: absolute;
      z-index: -1;
      top: 0;
      right: -10px;
      height: 16px;
      width: 16px;
    }

    @media screen and (max-width: 767px) {
      display: none;
    }
  }

  &__tasty {
    position: absolute;
    bottom: 0;
    right: 5%;
    z-index: 2;
    @media screen and (max-width: 767px) {
      display: none;
    }

    @media screen and (min-width: 1280px) {
      width: 293px;
      height: 232px;
      right: 9%;
    }
  }

  &__milk {
    position: absolute;
    bottom: 28px;
    left: 38px;
    width: 124px;
    height: 126px;

    @media screen and (max-width: 767px) {
      display: none;
    }

    @media screen and (min-width: 1280px) {
      width: 188px;
      height: 191px;
      left: 77px;
      bottom: 45px;
    }
  }
}
